.header__title {
  letter-spacing: -2px;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-size: 52px;
  font-weight: 600;
  line-height: 120%;
}

.header__text {
  letter-spacing: 0;
  color: #fff;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 150%;
}

.header__button {
  background-color: #2060f6;
  border-radius: 8px;
  grid-column: 1 / -1;
  place-items: center;
  height: 50px;
  text-decoration: none;
  transition: transform .3s;
  display: grid;
}

@media (width >= 640px) {
