    width: 264px;
  }
}

.header__button:hover {
  transform: scale(.95);
}

.header__button-text {
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 600;
}

.header__bottom {
  grid-column: 1 / -1;
  width: 100%;
  min-height: 390px;
  position: relative;
}

@media (width >= 1024px) {
  .header__bottom {
    grid-column: 7 / -1;
    min-height: 680px;
  }
}

.header__photo {
  opacity: 0;
  z-index: 1;
  background-position: center;
  background-size: cover;
  border-radius: 30px;
  width: 100%;
  height: 100%;
  transition: opacity .5s;
  position: absolute;
}

.header__photo.active {
  opacity: 1;
  z-index: 3;
}

.header__photo-1 {
  z-index: 1;
  background-image: url("photo-slider-clock.2c12cbc4.jpg");
}

.header__photo-2 {
  z-index: 2;
  background-image: url("slide-img-1.ce339bf7.jpg");
}

.header__photo-3 {
  z-index: 3;
  background-image: url("slider_photo_3.3c4a5665.jpg");
}

@media (width >= 1024px) {
  .header__photo {
    border-radius: 30px 0 0;
    border-end-end-radius: 30px;
  }
}

.icon {
  background-size: cover;
  width: 24px;
  height: 24px;
  transition: transform .3s;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--menu {
  background-image: url("Menu.0fcc0e0f.svg");
}

@media (width >= 1024px) {
  .icon--menu {
    display: none;
  }
}

.icon--close {
  background-image: url("icon-close.83c4b107.svg");
}

.icon--item {
  background-image: url("icon-item.058de16e.svg");
}

.top-bar {
  grid-column: 1 / -1;
  padding-top: 18px;
}

@media (width >= 1024px) {
  .top-bar {
    padding-block: 48px;
  }
}

.top-bar__content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.top-bar__link {
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  transition: transform .3s;
  position: relative;
}

.top-bar__link:hover {
  transform: scale(1.2);
}

.top-bar__link:after {
  content: "";
  transform-origin: 0;
  background-color: #2060f6;
  width: 100%;
  height: 1px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.top-bar__link:hover:after {
  transform: scale(1);
}

.top-bar__icons {
  align-items: center;
  gap: 32px;
  display: flex;
}

.top-bar__logo {
  transition: transform .3s;
  display: flex;
}

.top-bar__logo:hover {
  transform: scale(1.2);
}

.slider {
  z-index: 5;
  position: absolute;
  bottom: 24px;
  left: 20px;
}

.slider__body {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.slider__buttons {
  gap: 16px;
  display: flex;
}

.slider__block {
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  transition: transform .3s;
}

.slider__block:hover {
  transform: scale(1.2);
}

.slider__right {
  justify-content: center;
  display: flex;
}

.slider__right:before {
  content: ">";
}

.slider__left {
  justify-content: center;
  display: flex;
}

.slider__left:before {
  content: "<";
  width: 18px;
}

.slider__info {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.slider__intro {
  color: #fff;
  letter-spacing: 3px;
  text-align: center;
  text-transform: uppercase;
  font-family: Open Sans, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
}

.slider__text {
  color: #fff;
  letter-spacing: 0;
  font-family: Open Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}

.slider__link {
  text-decoration: none;
}

.slider__link:focus {
  color: #000;
}

.aside-menu {
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  background-color: #5b5b5b;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.aside-menu:target {
  opacity: 1;
  pointer-events: all;
  right: 0;
  transform: translateX(0);
}

.aside-menu:target .nav {
  display: flex;
}

.aside-menu__container {
